import { Button, Form, Input, Radio } from 'antd';
import React, { useEffect, useState } from 'react';
import Player from '../../components/Player/Player';
import Uploader from '../../components/Uploader/Uploader';
import './index.css';

export default function Text (props) {
    const {ffmpeg, fetchFile} = props
    const [url, setUrl] = useState('')
    const [imgFile, setImgFile] = useState(null)
    const [file, setFile] = useState(null)
    const loadVideo = async () => {
        console.log('')
        ffmpeg.FS('writeFile', file.name, await fetchFile(file));
        const data = ffmpeg.FS('readFile', file.name);
        let originFileUrl = URL.createObjectURL( new Blob( [data.buffer] , { type: 'video/mp4' } ) );
        setUrl(originFileUrl)
    }
    useEffect(() => {
        if (file) {
            loadVideo()
        }
        // 
    }, [file])
    // 
    const addText = async (values) => {
        if (file && imgFile) {
            console.log('click', file ,imgFile)
            console.log('', values.picStart, values.picEnd)
            ffmpeg.FS('writeFile', imgFile.name, await fetchFile(imgFile));
            const filterCmd = '[1:v]format=rgba,rotate=' + `'${values.deg}:c=0x00000000:ow=hypot(iw,ih):oh=ow'` + `[out];[0:v][out]overlay=x=${values.xPos}:y=${values.yPos}:enable=` + `'between(t,${values.picStart},${values.picEnd})'`
            const cmd= '-i '+ file.name + ' -i '+ imgFile.name + ' -filter_complex '+ filterCmd +' 1.mp4'
            if(!ffmpeg.isLoaded()) {
                alert('')
                return
            }
            let args1 = cmd.split(' ')
            console.log('args1',args1)
            await ffmpeg.run(...args1)
            const data = ffmpeg.FS( 'readFile' , '1.mp4' );
            let u1 = URL.createObjectURL( new Blob( [data.buffer] , { type: 'video/mp4' } ) );
            setUrl(u1)
        }
    }
    const onFinished = (values) => {
        let canvas = document.getElementById('canvas')
        const ctx = canvas.getContext('2d');
        ctx.textAlign = 'center' //
        ctx.textBaseline = 'middle'
        if (values.background) {
            ctx.rect(0,0,150,100);
            ctx.fillStyle = values.background
            ctx.fill()
        }
        ctx.font = values.font // 16 Arial
        ctx.fillStyle = values.color //
        if (values.art === 1) {
            var gradient=ctx.createLinearGradient(0,0,canvas.width,0);
            gradient.addColorStop("0","magenta");
            gradient.addColorStop("0.5","blue");
            gradient.addColorStop("1.0","red");
            // 
            ctx.strokeStyle=gradient;
            ctx.strokeText(values.text,75,38);
            let artUrl = canvas.toDataURL()
            setImgFile(base64ToFile(artUrl))
            return
        }
        ctx.fillText(values.text, 75, 38) //fillText(, x, y, )
        let textUrl = canvas.toDataURL()
        console.log('textUrl', textUrl, base64ToFile(textUrl))
        setImgFile(base64ToFile(textUrl))
    }
    //base64blob
    const dataURLtoBlob = (dataurl) => { 
        let arr = dataurl.split(','),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
        while (n--) {
            u8arr[n] = bstr.charCodeAt(n);
        }
        return new Blob([u8arr], { type: mime });
    }
    //blobfile
    const blobToFile = (theBlob, fileName) => {
        theBlob.lastModifiedDate = new Date();
        theBlob.name = fileName;
        return theBlob;
    }
    // base64file
    const base64ToFile = (base64Url) => {
        let blob = dataURLtoBlob(base64Url)
        let file = blobToFile(blob, 'textImg.png')
        console.log('fff', file)
        return file
    }

    return (
        <div className='text-wrap'>
            <h3></h3>
            <div className='canvas-video-wrap'>
                <div className='canvas-wrap'>
                    <canvas style={{width:'300px'}} id='canvas'></canvas>
                    <Form
                        className='items'
                        onFinish={onFinished}
                    >
                        <Form.Item
                            label=''
                            name='text'
                        >
                            <Input id='text' placeholder=''/>
                        </Form.Item>
                        <Form.Item
                            label=''
                            name='font'
                        >
                            <Input id='font' placeholder=''/>
                        </Form.Item>
                        <Form.Item
                            label=''
                            name='color'
                        >
                            <Input id='color' placeholder=''/>
                        </Form.Item>
                        <Form.Item
                            label=''
                            name='background'
                        >
                            <Input id='background' placeholder=''/>
                        </Form.Item>
                        <Form.Item
                            label=''
                            name='art'
                        >
                             <Radio.Group>
                                <Radio value={1}></Radio>
                                <Radio value={2}></Radio>
                            </Radio.Group>
                        </Form.Item>
                        <Button style={{width: '100px'}} htmlType='submit'></Button>

                    </Form>
                    <br></br>
                    <Uploader setFile={setFile}/>
                    <Form
                        className='items'
                        onFinish={addText}
                    >
                        <Form.Item
                            label=''
                            name='picStart'
                        >
                            <Input id='picStart' placeholder=''/>
                        </Form.Item>
                        <Form.Item
                            label=''
                            name='picEnd'
                        >
                            <Input id='picEnd' placeholder=''/>
                        </Form.Item>
                        <Form.Item
                            label='X'
                            name='xPos'
                        >
                            <Input id='xPos' placeholder='X'/>
                        </Form.Item>
                        <Form.Item
                            label='Y'
                            name='yPos'
                        >
                            <Input id='yPos' placeholder='Y'/>
                        </Form.Item>
                        <Form.Item
                            label=''
                            name='deg'
                        >
                            <Input id='deg' placeholder=''/>
                        </Form.Item>
                        <Button style={{width: '100px'}} htmlType='submit'></Button>
                    </Form>

                </div>
                
                <Player url={url}/>
            </div>
        </div>
    )
}
